html {
  margin: 0 1em;
}
body {
  background-color: #315c2b;
  color: linen;
}
div {
  margin: 1em;
}
ul,
li {
  margin: 0.1em;
  padding: 0.1em;
}

#bio {
  display: flex;
  margin: auto;
  border-style: solid;
  border-color: linen;
  border-width: 0.2rem;
  padding: 1rem 2rem;
  width: 90%;
}

.skillset {
  display: flex;
  flex-direction: row;
}

/* tooltip */
/* Tooltip container */
.tooltip {
  position: relative;
  display: inline-block;
  border-bottom: 1px dotted black; /* If you want dots under the hoverable text */
}

/* Tooltip text */
.tooltip .tooltiptext {
  visibility: hidden;
  width: 120px;
  background-color: #555;
  color: #fff;
  text-align: center;
  padding: 5px 0;
  border-radius: 6px;

  /* Position the tooltip text */
  position: absolute;
  z-index: 1;
  bottom: 125%;
  left: 50%;
  margin-left: -60px;

  /* Fade in tooltip */
  opacity: 0;
  transition: opacity 0.3s;
}

/* Tooltip arrow */
.tooltip .tooltiptext::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: #555 transparent transparent transparent;
}

/* Show the tooltip text when you mouse over the tooltip container */
.tooltip:hover .tooltiptext {
  visibility: visible;
  opacity: 1;
}
